import React, { useEffect, useState } from "react";
import axios from "axios";
import "./ApprovedVendorsView.css";

function ApprovePoPerVendor({dialogRef, updateDialogPosition }) {
  const user = JSON.parse(localStorage.getItem("user"));
  const [vendors, setVendors] = useState([]);
  const [selectedVendor, setSelectedVendor] = useState(null);
  const [message, setMessage] = useState("");
  const [loading, setLoading] = useState(true);
  const [showVendorDialog, setShowVendorDialog] = useState(false);

  useEffect(() => {
    const fetchVendorsWithItems = async () => {
      try {
        const response = await axios.get(
          `https://autotask-backend.onrender.com/api/po/finalized-vendors`,
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        );
        setVendors(response.data);
        console.log("data", response.data);
      } catch (err) {
        console.error("Fetch error:", err);
        setMessage("Error fetching vendors with approved PO items.");
      } finally {
        setLoading(false);
      }
    };

    if (user?.email) fetchVendorsWithItems();
    else {
      setMessage("User not logged in or email missing.");
      setLoading(false);
    }
  }, [user?.email]);
useEffect(() => {
    window.addEventListener('resize', updateDialogPosition);

    // Initial positioning
    updateDialogPosition();

    return () => {
      window.removeEventListener('resize', updateDialogPosition);
    };
  }, [showVendorDialog]);
  const handleVendorSelection = (vendor) => {
    setSelectedVendor(vendor);
    setMessage("");
  };

  const handleAction = async (vendorId, action) => {
    try {
      await axios.post(
        `https://autotask-backend.onrender.com/api/vendors/${vendorId}/po-${action}`,
        { approverEmail: user.email },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      setMessage(`✅ PO for vendor ${action === "approve" ? "approved" : "rejected"} successfully.`);
      setVendors((prev) => prev.filter((v) => v.vendorId !== vendorId));
      setSelectedVendor(null);
    } catch (err) {
      console.error(err);
      setMessage(`❌ Failed to ${action} PO for vendor.`);
    }
  };

  const openPdfDialog = (vendorId) => {
    
    setShowVendorDialog(true);
  };

  const closePdfDialog = () => {
    setShowVendorDialog(false);
   
  };

  if (loading) return <p>Loading vendors...</p>;

  return (
    <div>
    <div className={`approved-vendors-container ${showVendorDialog ? 'blurred' : ''}`}>
      <h2>PO Approval - Vendors</h2>
      {message && <p className="message">{message}</p>}
      <div className="details-list-control">
        <div className="list-control">
          <h3 style={{ textAlign: "center" }}>Vendors</h3>
          <div className="vendor-list">
            {vendors.map((vendor) => (
              <div
                key={vendor.vendorId}
                className={`vendor-item ${selectedVendor?.vendorId === vendor.vendorId ? "active" : ""}`}
                onClick={() => handleVendorSelection(vendor)}
              >
                {vendor.vendorDetail?.vendorName || "Unnamed Vendor"} <br />
                <small>ID: {vendor.vendorId}</small>
              </div>
            ))}
          </div>
        </div>

        <div className="vendor-details-section">
          {selectedVendor ? (
            <div className="vendor-details">
              <div className="view-pdf">
                <button
                    
                    onClick={() => openPdfDialog(selectedVendor.vendorDetail?.vendorId)}
                  >
                    VIEW PDF
                  </button>
              </div>
              <h3>Vendor Details</h3>
              <p><strong>Name:</strong> {selectedVendor.vendorDetail?.vendorName}</p>
              <p><strong>Type:</strong> {selectedVendor.vendorDetail?.vendorType}</p>
              <p><strong>GST:</strong> {selectedVendor.vendorDetail?.gstNumber}</p>
              <p><strong>PAN:</strong> {selectedVendor.vendorDetail?.panNumber}</p>

              <h4>Contacts:</h4>
              <ul>
                {selectedVendor.vendorDetail?.contacts?.map((c, idx) => (
                  <li key={idx}>{c.name} — {c.email} — {c.phone} — {c.designation}</li>
                ))}
              </ul>

              <h4>Bank Accounts:</h4>
              <ul>
                {selectedVendor.vendorDetail?.bankAccounts?.map((b, idx) => (
                  <li key={idx}>{b.bankName} — {b.accountNumber} — {b.ifscCode} — {b.accountType}</li>
                ))}
              </ul>

              <h3>Items for PO Approval</h3>
              {selectedVendor.items.map((item, index) => (
                <div key={index} style={{ marginBottom: "15px" }}>
                  <h4>Item-{index + 1}</h4>
                  <p><strong>Material:</strong> {item.material}</p>
                  <p><strong>Quantity:</strong> {item.quantity} {item.unit}</p>
                  <p><strong>Delivery Date:</strong> {new Date(item.deliveryDate).toLocaleDateString()}</p>
                  <p><strong>Cost Center:</strong> {item.costCenter}</p>
                  <p><strong>Description:</strong> {item.description}</p>
                  <p><strong>PR ID:</strong> {item.prId}</p>
                  <p><strong>Item ID:</strong> {item.itemId}</p>
                  <p><strong>Total Price: ₹</strong> {item.price}</p>
                </div>
              ))}

              <div className="button-group">
                <button
                  className="approve-btn"
                  onClick={() => handleAction(selectedVendor.vendorId, "approve")}
                >
                  ✅ Approve PO
                </button>
                <button
                  className="reject-btn"
                  onClick={() => handleAction(selectedVendor.vendorId, "reject")}
                >
                  ❌ Reject PO
                </button>
              </div>
            </div>
          ) : (
            <p>Select a vendor to see details</p>
          )}
        </div>
      </div>


      
    

    </div>
          {showVendorDialog && (
            <div 
                className="vendor-dialog"
                ref={dialogRef}
                style={{ transform: 'translateX(-50%) translateY(-50%)', top: '50%' }}
            >

                <button 
                onClick={closePdfDialog} 
                 className="close-dialog-btn top-right"
                >
                      ❌
                </button>
                <h2>Purchase Order ({selectedVendor.vendorDetail?.vendorName})</h2>
     
                {message && <p className="message">{message}</p>}

                {loading ? (
                <p>Loading...</p>
                ) : (
                <div>

                  //render pdf here
                </div>
                )}
            </div>
            )}
    </div>
  );
}

export default ApprovePoPerVendor;
