

import React, { useState, useEffect } from "react";
import "./AddChargeDialog.css";
import { startTransition } from "react";

const TEMPLATES = [
  {
    id: "template1",
    label: "GST Combo",
    charges: [
        { label: "Discount", value: null, description: "5%", percent: -5 },
      { label: "CGST", value: null, description: "9%", percent: 9 },
      { label: "SGST", value: null, description: "9%", percent: 9 },
      
    ],
  },
  {
    id: "template2",
    label: "GST Only",
    charges: [
      { label: "IGST", value: null, description: "18%", percent: 18 },
    ],
  },
  {
    id: "template3",
    label: "Discount Only",
    charges: [
      { label: "Discount", value: null, description: "10%", percent: -10 },
    ],
  },
];

function AddChargeDialog({ open, onClose, subTotal, charges, setCharges,totalPOValue, setTotalPOValue, }) {
  const [selectedTab, setSelectedTab] = useState("template");
  const [selectedTemplateId, setSelectedTemplateId] = useState(null);
  const [manualCharges, setManualCharges] = useState([]);


const handleTemplateSelect = (template) => {
  setSelectedTemplateId(template.id); // Only preview
};

const handleTemplateCheckbox = (template, isChecked) => {
  if (isChecked) {
    console.log("subtotal:" ,subTotal)
    const computedCharges = template.charges.map((c) => {
      const percent = Number(c.percent);
      const value = isNaN(percent) ? 0 : (subTotal * percent) / 100;

      return {
        label: c.label,
        value: Number(value.toFixed(2)),
        description: c.description,
      };
    });

    // Only set charges if at least one value is numeric (avoid empty/NaN states)
    const validCharges = computedCharges.filter(c => !isNaN(c.value));

    setManualCharges(validCharges);
    setCharges(validCharges, true);
    setSelectedTemplateId(template.id);

    // const total = subTotal + validCharges.reduce((acc, c) => acc + c.value, 0);
    // setTotalPOValue(Number(total.toFixed(2)));

    const total = Number(subTotal || 0) + validCharges.reduce((acc, c) => acc + Number(c.value || 0), 0);
    setTotalPOValue(Number(total.toFixed(2)));

  } else {
    setManualCharges([]);
    setCharges([], true);
    setSelectedTemplateId(null);
    setTotalPOValue(subTotal); // Reset to base value
  }
};

  const handleManualChange = (index, field, value) => {
    const updated = [...manualCharges];
    updated[index][field] = field === "value" ? Number(value) : value;
    setManualCharges(updated);

    // Check for mismatch with selected template
    const selectedTemplate = TEMPLATES.find(t => t.id === selectedTemplateId);
    if (
      selectedTemplate &&
      !matchTemplateWithManual(selectedTemplate, updated)
    ) {
      setSelectedTemplateId(null); // Unselect template
    }

    // setCharges(updated);
    setCharges(updated, false)
  };

  const matchTemplateWithManual = (template, manual) => {
    if (template.charges.length !== manual.length) return false;
    const expected = template.charges.map((c) => ({
      label: c.label,
      value: Number(((subTotal * c.percent) / 100).toFixed(2)),
      description: c.description,
    }));
    return expected.every((e, i) =>
      e.label === manual[i].label &&
      e.value === manual[i].value &&
      e.description === manual[i].description
    );
  };

  // const addManualRow = () => {
  //   setManualCharges([
  //     ...manualCharges,
  //     { label: "", value: 0, description: "" },
  //   ]);
  // };

  const addManualRow = () => {
  const updated = [
    ...manualCharges,
    { label: "", value: 0, description: "" },
  ];
  setManualCharges(updated);
  setSelectedTemplateId(null); // Editing = unselect template
  setCharges(updated); // Sync charges with manual input
};


  const removeManualRow = (index) => {
    const updated = [...manualCharges];
    updated.splice(index, 1);
    setManualCharges(updated);
    setSelectedTemplateId(null); // Editing = unselect template
    setCharges(updated);
  };

  useEffect(() => {
    // Initialize manual charges on open
    if (open) {
      setManualCharges([...charges]);
      const matchingTemplate = TEMPLATES.find((t) =>
        matchTemplateWithManual(t, charges)
      );
      setSelectedTemplateId(matchingTemplate?.id || null);
    }
  }, [open, charges]);

  if (!open) return null;

  return (
    <div className="dialog-backdrop">
      <div className="dialog">
        <h2>Add Charges</h2>
        <div className="tabs">
          <button
            className={selectedTab === "template" ? "active" : ""}
            onClick={() => setSelectedTab("template")}
          >
            Template
          </button>
          <button
            className={selectedTab === "manual" ? "active" : ""}
            onClick={() => setSelectedTab("manual")}
          >
            Manual
          </button>
        </div>

        {selectedTab === "template" && (
          <div className="template-tab">

            <div className="template-list">
  {TEMPLATES.map((template) => {
    const isChecked = selectedTemplateId === template.id;
 
  const areChargesEqual = (a, b) => {
  return (
    a.length === b.length &&
    a.every(
      (charge, i) =>
        charge.label === b[i].label &&
        charge.description === b[i].description
    )
  );
};



    return (


 <div
  key={template.id}
  className={`template-item ${isChecked ? "selected" : ""}`}
  onClick={() => handleTemplateSelect(template)}
>
  <span onClick={(e) => e.stopPropagation()}>
    <input
      type="checkbox"
      checked={areChargesEqual(charges, template.charges)}

      onChange={(e) =>
        handleTemplateCheckbox(template, e.target.checked)
      }
    />
  </span>
  <span className="template-desc">
    {template.charges.map((c) => `${c.label}: ${c.description}`).join(", ")}
  </span>
</div>


    );
  })}
</div>


            {selectedTemplateId && (
              <div className="template-content">
                <h4>Charge Breakdown</h4>
                <ul>
                  {TEMPLATES.find((t) => t.id === selectedTemplateId)?.charges.map(
                    (c, i) => (
                      <li key={i}>
                        {c.label}: ₹{((subTotal * c.percent) / 100).toFixed(2)} ({c.description})
                      </li>
                    )
                  )}
                </ul>
                <strong>
                  Total PO Value: ₹
                  {(
                    subTotal +
                    TEMPLATES.find((t) => t.id === selectedTemplateId)?.charges.reduce(
                      (acc, c) => acc + (subTotal * c.percent) / 100,
                      0
                    )
                  ).toFixed(2)}
                </strong>
              </div>
            )}
          </div>
        )}

        {selectedTab === "manual" && (
          <div className="manual-tab">
            {manualCharges.map((charge, index) => (
              <div key={index} className="manual-charge-row">
                <input
                  type="text"
                  placeholder="Label"
                  value={charge.label}
                  onChange={(e) =>
                    handleManualChange(index, "label", e.target.value)
                  }
                />
                <input
                  type="number"
                  placeholder="Value"
                  value={charge.value}
                  onChange={(e) =>
                    handleManualChange(index, "value", e.target.value)
                  }
                />
                <input
                  type="text"
                  placeholder="Description"
                  value={charge.description}
                  onChange={(e) =>
                    handleManualChange(index, "description", e.target.value)
                  }
                />
                <button onClick={() => removeManualRow(index)}>✕</button>
              </div>
            ))}
            <button onClick={addManualRow}>+ Add Charge</button>

            <div className="manual-totalpo-field">
                <label>Total PO Value:</label>
                <input
                    type="number"
                    value={totalPOValue}
                    onChange={(e) => setTotalPOValue(Number(e.target.value))}
                />
                </div>

          </div>
        )}

        <div className="dialog-actions">
          <button onClick={onClose}>Close</button>
        </div>
      </div>
    </div>
  );
}

export default AddChargeDialog;


