import React, { useEffect, useState } from "react";
import axios from "axios";
// import "./ApprovePr.css";
import "./ApprovedVendorsView.css";

function FinanceApproveVendors() {
  const user = JSON.parse(localStorage.getItem("user"));
  const [vendors, setVendors] = useState([]);
  const [selectedVendorIndex, setSelectedVendorIndex] = useState(null);
  const [loading, setLoading] = useState(true);
  const [message, setMessage] = useState("");

  useEffect(() => {
    const fetchPendingVendors = async () => {
      try {
        const response = await axios.get(
          `https://autotask-backend.onrender.com/api/vendors/pending`,
          {
            params: { approverEmail: user.email },
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        );
        setVendors(response.data);
      } catch (err) {
        console.error("Fetch error:", err);
        setMessage("Error fetching pending vendor requests.");
      } finally {
        setLoading(false);
      }
    };

    if (user?.email) fetchPendingVendors();
    else {
      setMessage("User not logged in or email missing.");
      setLoading(false);
    }
  }, [user?.email]);

  const handleVendorAction = async (vendorId, action) => {
    try {
      await axios.post(
        `https://autotask-backend.onrender.com/api/vendors/${vendorId}/${action}`,
        { approverEmail: user.email },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      setMessage(`✅ Vendor ${action === "approve" ? "approved" : "rejected"} successfully.`);
      setVendors((prev) => prev.filter((v) => v._id !== vendorId));
      setSelectedVendorIndex(null);
    } catch (err) {
      console.error(err);
      setMessage(`❌ Failed to ${action} vendor.`);
    }
  };

  const renderVendorDetails = (vendor) => (
    <div className="vendor-details">
      <h3>{vendor.vendorName}</h3>
      <p><strong>Vendor Type:</strong> {vendor.vendorType}</p>
      <p><strong>GST Number:</strong> {vendor.gstNumber}</p>
      <p><strong>PAN Number:</strong> {vendor.panNumber}</p>

      <div>
        <h4>Contacts:</h4>
        <ul>
          {vendor.contacts.map((c, i) => (
            <li key={i}>{c.name} — {c.email} — {c.phone} — {c.designation}</li>
          ))}
        </ul>
      </div>

      <div>
        <h4>Bank Accounts:</h4>
        <ul>
          {vendor.bankAccounts.map((b, i) => (
            <li key={i}>{b.bankName} — {b.accountNumber} — {b.ifscCode} — {b.accountType}</li>
          ))}
        </ul>
      </div>

      <div className="button-group" style={{ marginTop: "20px" }}>
        <button
          className="approve-btn"
          onClick={() => handleVendorAction(vendor._id, "approve")}
        >
          ✅ Approve Vendor
        </button>
        <button
          className="reject-btn"
          onClick={() => handleVendorAction(vendor._id, "reject")}
        >
          ❌ Reject Vendor
        </button>
      </div>
    </div>
  );

  return (
    <div className="approved-vendors-container">
      <h2>Finance Approval - Pending Vendor Onboarding</h2>
      {message && <p className="message">{message}</p>}
      {loading ? (
        <p>Loading vendors...</p>
      ) : (
        <div className="details-list-control">
          <div className="list-control">
            <div className="vendor-list">
              {vendors.length === 0 ? (
                <p>No pending vendors to approve.</p>
              ) : (
                vendors.map((vendor, idx) => (
                  <div
                    key={vendor._id}
                    className={`vendor-item ${selectedVendorIndex === idx ? "active" : ""}`}
                    onClick={() => setSelectedVendorIndex(idx)}
                  >
                    {vendor.vendorName} | {vendor.vendorType}
                  </div>
                ))
              )}
            </div>
          </div>

          <div className="vendor-details-section">
            {selectedVendorIndex !== null ? (
              renderVendorDetails(vendors[selectedVendorIndex])
            ) : (
              <p>Select a vendor to view details.</p>
            )}
          </div>
        </div>
      )}
    </div>
  );
}

export default FinanceApproveVendors;
