import React, { useState, useEffect } from "react";
import axios from "axios";
import "./ApprovedVendorsView.css";

function ApprovedVendorsView() {
  const user = JSON.parse(localStorage.getItem("user"));
  const [vendors, setVendors] = useState([]);
  const [selectedVendorIndex, setSelectedVendorIndex] = useState(null);
  const [loading, setLoading] = useState(true);
  const [message, setMessage] = useState("");

  useEffect(() => {
    const fetchApprovedVendors = async () => {
      try {
        const response = await axios.get(
          `https://autotask-backend.onrender.com/api/vendors/approved`,
          {
            params: { approverEmail: user.email },
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        );
        setVendors(response.data);
      } catch (err) {
        console.error("Error fetching approved vendors:", err);
        setMessage("Failed to fetch approved vendors.");
      } finally {
        setLoading(false);
      }
    };

    if (user?.email) {
      fetchApprovedVendors();
    } else {
      setMessage("User not logged in or email missing.");
      setLoading(false);
    }
  }, [user?.email]);

  const renderVendorDetails = (vendor) => (
    <div className="vendor-details">
      <h3>{vendor.name}</h3>
      <p><strong>Vendor Type:</strong> {vendor.vendorType}</p>
      <p><strong>GST Number:</strong> {vendor.gstNumber}</p>
      <p><strong>PAN Number:</strong> {vendor.panNumber}</p>
      <p><strong>Contacts:</strong></p>
      <ul>
        {vendor.contacts?.map((contact, idx) => (
          <li key={idx}>
            {contact.name} - {contact.email} - {contact.phone}
          </li>
        ))}
      </ul>
      <p><strong>Bank Accounts:</strong></p>
      <ul>
        {vendor.bankAccounts?.map((account, idx) => (
          <li key={idx}>
            A/C: {account.accountNumber} - IFSC: {account.ifscCode}
          </li>
        ))}
      </ul>
    </div>
  );

  return (
    <div className="approved-vendors-container">
      <h2>Approved Vendors</h2>
      {message && <p className="message">{message}</p>}
      {loading ? (
        <p>Loading vendors...</p>
      ) : (
        <div className="details-list-control">
          <div className="list-control">
            <div className="vendor-list">
              {vendors.length === 0 ? (
                <p>No approved vendors found.</p>
              ) : (
                vendors.map((vendor, idx) => (
                  <div
                    key={vendor.id}
                    className={`vendor-item ${selectedVendorIndex === idx ? "active" : ""}`}
                    onClick={() => setSelectedVendorIndex(idx)}
                  >
                    {vendor.name} | {vendor.vendorType}
                  </div>
                ))
              )}
            </div>
          </div>

          <div className="vendor-details-section">
            {selectedVendorIndex !== null ? (
              renderVendorDetails(vendors[selectedVendorIndex])
            ) : (
              <p>Select a vendor to view details.</p>
            )}
          </div>
        </div>
      )}
    </div>
  );
}

export default ApprovedVendorsView;
