const handleVendorCheckboxToggle = (vendor) => {

  const isAlreadySelected =
    selectedVendors[currentTileIndex]?.some((v) => v.id === vendor._id) ?? false;

  // 1. Update selected vendors
  setSelectedVendors((prev) => {
    const currentVendors = prev[currentTileIndex] || [];
    const updatedVendors = isAlreadySelected
      ? currentVendors.filter((v) => v.id !== vendor._id)
      : [...currentVendors, { ...vendor, totalValue: '' }];

    return {
      ...prev,
      [currentTileIndex]: updatedVendors,
    };
  });

  // 2. Update selected items
  setSelectedItems((prev) => {
    const currentSelected = prev[currentTileIndex] || [];
    const updatedSelection = isAlreadySelected
      ? currentSelected.filter((id) => id !== vendor._id)
      : [...currentSelected, vendor._id];

    return {
      ...prev,
      [currentTileIndex]: updatedSelection,
    };
  });


setVendorDetails((prev) => {
  const current = prev[currentTileIndex] || [];

  if (isAlreadySelected) {
    // Remove vendor
    const updatedForTile = current.filter((_, i) =>
      selectedVendors[currentTileIndex]?.[i]?._id !== vendor._id
    );

    const updated = [...prev];
    updated[currentTileIndex] = updatedForTile;
    return updated;
  } else {
    // Add vendor and sync with splitLine
    const lines = [
      {
        charges: [], // Initialize charges per line
        subtotal: 0,  // Initialize subtotal per line
        
      },
    ];

    const updatedForTile = [...current, { lines }];
    const updated = [...prev];
    updated[currentTileIndex] = updatedForTile;
    return updated;
  }
});


setTermsDetails((prev) => {
  const current = prev[currentTileIndex] || [];

  if (isAlreadySelected) {
    // Remove vendor
    const updatedForTile = current.filter((_, i) =>
      selectedVendors[currentTileIndex]?.[i]?._id !== vendor._id
    );

    const updated = [...prev];
    updated[currentTileIndex] = updatedForTile;
    return updated;
  } else {
    // Add vendor and sync with splitLine
    const lines = [
      {
        
        terms: [], // Initialize terms per line
        
      },
    ];

    const updatedForTile = [...current, { lines }];
    const updated = [...prev];
    updated[currentTileIndex] = updatedForTile;
    return updated;
  }
});

  // 4. Update splitLine immutably
  setSplitLine((prev) => {
    const current = prev[currentTileIndex] || [];

    const updatedForTile = isAlreadySelected
      ? current.filter((_, i) => selectedVendors[currentTileIndex]?.[i]?._id !== vendor._id)
      : [
          ...current,
          {
            lines: [
              {
                product: '',
                hsnSac: '',
                quantity: '',
                price: '',
                pricePerUnit: '',
                unit: '',
              },
            ],
          },
        ];

    const updated = [...prev];
    updated[currentTileIndex] = updatedForTile;
    return updated;
  });
};




const handleDeleteVendor = (tileIndex, vendorIndex) => {
  setSelectedVendors((prev) => {
    const updatedVendors = [...(prev[tileIndex] || [])];
    const vendorIdToRemove = updatedVendors[vendorIndex]?._id;
    updatedVendors.splice(vendorIndex, 1);

    // Update selectedItems here
    setSelectedItems((prevItems) => {
      const updatedSelected = (prevItems[tileIndex] || []).filter(
        (id) => id !== vendorIdToRemove
      );

      return {
        ...prevItems,
        [tileIndex]: updatedSelected,
      };
    });

    setVendorDetails((prevDetails) => {
      const updatedDetails = [...prevDetails];
      if (updatedDetails[tileIndex]) {
        updatedDetails[tileIndex].splice(vendorIndex, 1);
      }
      return updatedDetails;
    });

    setTermsDetails((prevDetails) => {
      const updatedDetails = [...prevDetails];
      if (updatedDetails[tileIndex]) {
        updatedDetails[tileIndex].splice(vendorIndex, 1);
      }
      return updatedDetails;
    });

       // Reset splitLine
    setSplitLine((prevSplit) => {
      const updatedSplit = [...prevSplit];
      if (updatedSplit[tileIndex]) {
        updatedSplit[tileIndex].splice(vendorIndex, 1);
      }
      return updatedSplit;
    });

    return {
      ...prev,
      [tileIndex]: updatedVendors,
    };

    
  });

};

const handleDeleteLine = (tileIndex, vendorIndex, lineIndex) => {
  setSplitLine(prevSplitLine =>
    prevSplitLine.map((tile, tIdx) =>
      tIdx === tileIndex
        ? tile.map((vendor, vIdx) =>
            vIdx === vendorIndex
              ? {
                  ...vendor,
                  lines: vendor.lines.filter((_, lIdx) => lIdx !== lineIndex),
                }
              : vendor
          )
        : tile
    )
  );

   // Delete corresponding line (and its charges) from vendorDetails
  setVendorDetails(prevVendorDetails =>
    prevVendorDetails.map((tile, tIdx) =>
      tIdx === tileIndex
        ? tile.map((vendor, vIdx) =>
            vIdx === vendorIndex
              ? {
                  ...vendor,
                  lines: vendor.lines.filter((_, lIdx) => lIdx !== lineIndex),
                }
              : vendor
          )
        : tile
    )
  );

  setTermsDetails(prevVendorDetails =>
    prevVendorDetails.map((tile, tIdx) =>
      tIdx === tileIndex
        ? tile.map((vendor, vIdx) =>
            vIdx === vendorIndex
              ? {
                  ...vendor,
                  lines: vendor.lines.filter((_, lIdx) => lIdx !== lineIndex),
                }
              : vendor
          )
        : tile
    )
  );
};


