const mergedItemsMap = new Map();

poData.forEach((item, itemIndex) => {
  const vendorId = item.id;

  const items = selectedVendors[itemIndex] || [];

  items.forEach((vendor, vendorIndex) => {
    const vendorIdd = vendor.id || vendor._id;
    const lines = splitLine?.[itemIndex]?.[vendorIndex]?.lines || [];
    const chargeLines = vendorDetails?.[itemIndex]?.[vendorIndex]?.lines || [];
    const termLines = termsDetails?.[itemIndex]?.[vendorIndex]?.lines || [];

    const lineObjects = lines.map((line, lineIndex) => ({
      product: line.product || vendor.material,
      hsnSac: line.hsnSac,
      quantity: line.quantity,
      unitOfMeasurement: line.unit,
      pricePerUnit: line.pricePerUnit,
      grossPrice: line.price,
      charges: chargeLines?.[lineIndex]?.charges || [],
      terms: termLines?.[lineIndex]?.terms || [],
      itemId: vendor._id,
      prId: vendor.prId
    }));

    if (!mergedItemsMap.has(vendorId)) {
      mergedItemsMap.set(vendorId, {
        vendorId,
        vendorName: item.name,
        contacts: item.contacts,
        gstNumber: item.gstNumber,
        panNumber: item.panNumber,
        items: {}
      });
    }

    const itemEntry = mergedItemsMap.get(vendorId);

    itemEntry.items[vendorIdd] = {
      lines: lineObjects,
      totalValue: Number(vendor.totalValue || 0)
    };
  });
});

const payload = [
  {
    vendors: Array.from(mergedItemsMap.values())
  }
];



