// src/UserManagement.js
import React, { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom"; // ✅ added
import "./UserManagement.css";

const privilegeOptions = [
  "create_pr",
  "approve_pr",
  "view_pr",
  "manage_users",
];

function UserManagement() {
  const navigate = useNavigate(); // ✅ added
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    password: "",
    role: "",
    department: "",
    privileges: [],
  });

  const [message, setMessage] = useState("");

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    if (type === "checkbox") {
      setFormData((prev) => ({
        ...prev,
        privileges: checked
          ? [...prev.privileges, value]
          : prev.privileges.filter((priv) => priv !== value),
      }));
    } else {
      setFormData((prev) => ({ ...prev, [name]: value }));
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const token = localStorage.getItem("token");
      await axios.post(
        "https://autotask-backend.onrender.com/register",
        formData,
        { headers: { Authorization: `Bearer ${token}` } }
      );
      setMessage("✅ User created successfully");
    } catch (err) {
      setMessage("❌ Failed to create user");
    }
  };

  return (
    <div className="user-management-wrapper">
      {/* ✅ Home Button */}
      <div className="top-right-home">
        <button onClick={() => navigate("/protected")}>🏠 Home</button>
      </div>

      <h2>Create New User</h2>
        <form onSubmit={handleSubmit} className="user-form">
        <input name="name" placeholder="Name" onChange={handleChange} required />
        <input name="email" placeholder="Email" onChange={handleChange} required />
        <input name="password" placeholder="Password" type="password" onChange={handleChange} required />
        <select name="department" value={formData.department} onChange={handleChange} required>
            <option value="" disabled>Select Department</option>
            <option value="IT">IT</option>
            <option value="HR">HR</option>
            <option value="Finance">Finance</option>
            <option value="Marketing">Marketing</option>
        </select>

        <select name="role" value={formData.role} onChange={handleChange} required>
            <option value="" disabled>Select Role</option>
            <option value="Admin">Admin</option>
            <option value="Manager">Manager</option>
            <option value="Submitter">Submitter</option>
            <option value="Viewer">Viewer</option>
        </select>

        <div className="privileges-checkboxes">
            <label>Assign Privileges:</label>
            {privilegeOptions.map((priv) => (
            <label key={priv}>
                <input
                type="checkbox"
                name="privileges"
                value={priv}
                onChange={handleChange}
                />
                {priv}
            </label>
            ))}
        </div>

        <button type="submit">Create User</button>
        </form>

      {message && <p className="status-message">{message}</p>}
    </div>
  );
}

export default UserManagement;
