

import React, { useState, useEffect } from "react";
import axios from "axios";
import "./PendingPo.css";

function PendingPoView({goToCreateSelectedPo,goToCreateSelectedVendorPo, setPoData}) {
  const user = JSON.parse(localStorage.getItem("user"));
  const [pendingItems, setPendingItems] = useState([]);
  const [selectedItem, setSelectedItem] = useState(null);
  const [loading, setLoading] = useState(true);
  const [message, setMessage] = useState("");
  const [selectedItems, setSelectedItems] = useState([]);


  const [activeTab, setActiveTab] = useState("item"); // "item" or "vendor"
  const [vendorList, setVendorList] = useState([]);
  const [selectedVendors, setSelectedVendors] = useState([]);
  const [selectedVendorIndex, setSelectedVendorIndex] = useState(null);



      const handleSelectAll = () => {
        if (selectedItems.length === pendingItems.length) {
          setSelectedItems([]); // unselect all
        } else {
          setSelectedItems(pendingItems.map((_, idx) => idx));
        }
      };


        const handleVendorSelectAll = () => {
          if (selectedVendors.length === vendorList.length) {
            setSelectedVendors([]);
          } else {
            setSelectedVendors(vendorList.map((_, idx) => idx));
          }
        };

  

        const handleCreatePo = () => {
        
          if (activeTab === "item") {
            const filteredPoData = pendingItems.filter((_, index) =>
              selectedItems.includes(index)
            );
            setPoData(filteredPoData);
            console.log("filteredPoData (by item)", filteredPoData);
            goToCreateSelectedPo();
          } else if (activeTab === "vendor") {
            const filteredPoData = vendorList.filter((_, index) =>
              selectedVendors.includes(index)
            );
            setPoData(filteredPoData);
            console.log("filteredPoData (by vendor)", filteredPoData);
            goToCreateSelectedVendorPo();
          }

        };




        useEffect(() => {
          const fetchPendingPOs = async () => {
            try {
              const response = await axios.get(
                `https://autotask-backend.onrender.com/api/purchase-requests/items-with-pending-po?email=${user.email}`,
                {
                  headers: {
                    Authorization: `Bearer ${localStorage.getItem("token")}`,
                  },
                }
              );
              setPendingItems(response.data);
            } catch (err) {
              console.error("Fetch error:", err);
              setMessage("Error fetching pending PO items.");
            } finally {
              setLoading(false);
            }
          };

          if (user?.email) {
            fetchPendingPOs();
          } else {
            setMessage("User not logged in or email missing.");
            setLoading(false);
          }
        }, [user?.email]);




      useEffect(() => {
        const fetchApprovedVendors = async () => {
          try {
            const response = await axios.get(
              `https://autotask-backend.onrender.com/api/vendors/approved`,
              {
                params: { approverEmail: user.email },
                headers: {
                  Authorization: `Bearer ${localStorage.getItem("token")}`,
                },
              }
            );

            setVendorList(response.data); // direct assignment

          } catch (err) {
            console.error("Error fetching approved vendors:", err);
            setMessage("Failed to fetch approved vendors.");
          } finally {
            setLoading(false);
          }
        };

        if (user?.email) {
          fetchApprovedVendors();
        } else {
          setMessage("User not logged in or email missing.");
          setLoading(false);
        }
      }, [user?.email]);


      const renderVendorDetails = (vendor) => (
        <div className="item-details">
          <h3>{vendor.name}</h3>
          <p><strong>Type:</strong> {vendor.vendorType}</p>
          <p><strong>Status:</strong> {vendor.status}</p>
          <p><strong>GST:</strong> {vendor.gstNumber}</p>
          <p><strong>PAN:</strong> {vendor.panNumber}</p>

          <h4>Contacts:</h4>
          <ul>
            {vendor.contacts.map((c, idx) => (
              <li key={idx}>{c.name} - {c.designation} ({c.email}, {c.phone})</li>
            ))}
          </ul>

          <h4>Bank Accounts:</h4>
          <ul>
            {vendor.bankAccounts.map((b, idx) => (
              <li key={idx}>{b.bankName}, A/C: {b.accountNumber}, IFSC: {b.ifscCode}</li>
            ))}
          </ul>
        </div>
      );


      const renderItemDetails = (item) => (
        <div className="item-details">
          <h3>Material: {item.material}</h3>
          <p><strong>Item ID:</strong> {item._id}</p>
          <p><strong>PR ID:</strong> {item.prId}</p>
          <p><strong>Quantity:</strong> {item.quantity}</p>
          <p><strong>Unit:</strong> {item.unit}</p>
          <p><strong>Delivery Date:</strong> {new Date(item.deliveryDate).toLocaleDateString()}</p>
          <p><strong>Cost Center:</strong> {item.costCenter}</p>
          <p><strong>Description:</strong> {item.description}</p>
          <p><strong>PO Creation Status:</strong> {item.statusPo}</p>
          <p><strong>Assigned Status:</strong> {item.poCreationAssignedStatus}</p>
          <p><strong>Assigned By:</strong> {item.poCreationAssignedBy}</p>
          <p><strong>Assigned Type:</strong> {item.poCreationAssignedType}</p>
          <p><strong>Assigned Mechanism:</strong> {item.poCreationAssignedMechanism}</p>
        </div>
      );



return (
  <div className="pending-po-container">
    <div className="tab-buttons">
      <button
        className={activeTab === "item" ? "active" : ""}
        onClick={() => setActiveTab("item")}
      >
        Create by Item
      </button>
      <button
        className={activeTab === "vendor" ? "active" : ""}
        onClick={() => setActiveTab("vendor")}
      >
        Create by Vendor
      </button>
    </div>

    {activeTab === "item" ? (
      <>
        {/* <h2>Pending PO Items</h2> */}
        {/* {message && <p className="message">{message}</p>} */}
        {loading ? (
          <p>Loading...</p>
        ) : (
          <div className="details-list-control">
            <div className="list-control">
              <div className="po-controls">
                <button onClick={handleSelectAll}>Select All</button>
                <button onClick={handleCreatePo}>Create PO</button>
              </div>

              <div className="po-list">
                {pendingItems.length === 0 ? (
                  <p>No pending PO items found.</p>
                ) : (
                  pendingItems.map((item, idx) => (
                    <div
                      key={idx}
                      className={`po-item ${selectedItem === idx ? "active" : ""}`}
                      onClick={() => setSelectedItem(idx)}
                    >
                      <input
                        type="checkbox"
                        checked={selectedItems.includes(idx)}
                        onChange={() => {
                          setSelectedItems((prev) =>
                            prev.includes(idx)
                              ? prev.filter((i) => i !== idx)
                              : [...prev, idx]
                          );
                        }}
                        onClick={(e) => e.stopPropagation()}
                      />
                      <span className="po-item-text">
                        Item#: {item._id} | {item.material} — {item.quantity} {item.unit}
                      </span>
                    </div>
                  ))
                )}
              </div>
            </div>

            <div className="po-details">
              {selectedItem !== null ? (
                renderItemDetails(pendingItems[selectedItem])
              ) : (
                <p>Select an item to view details.</p>
              )}
            </div>
          </div>
        )}
      </>
    ) : (
      <div className="details-list-control">
        <div className="list-control">
          <div className="po-controls">
            <button onClick={handleVendorSelectAll}>Select All</button>
            <button onClick={handleCreatePo}>Create PO</button>
          </div>

          <div className="po-list">
            {vendorList.length === 0 ? (
              <p>No vendors found.</p>
            ) : (
              vendorList.map((vendor, idx) => (
                <div
                  key={idx}
                  className={`po-item ${selectedVendorIndex === idx ? "active" : ""}`}
                  onClick={() => setSelectedVendorIndex(idx)}
                >
                  <input
                    type="checkbox"
                    checked={selectedVendors.includes(idx)}
                    onChange={() => {
                      setSelectedVendors((prev) =>
                        prev.includes(idx)
                          ? prev.filter((i) => i !== idx)
                          : [...prev, idx]
                      );
                    }}
                    onClick={(e) => e.stopPropagation()}
                  />
                  <span className="po-item-text">
                    {vendor.name} ({vendor.vendorType})
                  </span>
                </div>
              ))
            )}
          </div>
        </div>

        <div className="po-details">
          {selectedVendorIndex !== null ? (
            renderVendorDetails(vendorList[selectedVendorIndex])
          ) : (
            <p>Select a vendor to view details.</p>
          )}
        </div>
      </div>
    )}
  </div>
);
}

export default PendingPoView;
