import React, { useState, useEffect } from "react";
import "./AddTermsDialog.css"; // optional styling

const termTemplates = [
  {
    name: "Standard Terms",
    terms: [
      { label: "Delivery", description: "Within 7 working days from PO date" },
      { label: "Payment", description: "Net 30 days from invoice date" }
    ]
  },
  {
    name: "Urgent Terms",
    terms: [
      { label: "Delivery", description: "Within 3 working days" },
      { label: "Penalty", description: "1% per day late" }
    ]
  }
];

export default function AddTermsDialog({
  open,
  onClose,
  terms,
  setTerms
}) {
  const [activeTab, setActiveTab] = useState("template");
  const [manualTerms, setManualTerms] = useState([]);
  const [selectedTemplate, setSelectedTemplate] = useState(null);

  useEffect(() => {
    setManualTerms(terms || []);
  }, [terms]);

  const handleTemplateSelect = (template) => {
    setSelectedTemplate(template.name);
    setManualTerms(template.terms);
    setTerms(template.terms);
  };

  const handleManualChange = (index, key, value) => {
    const updated = manualTerms.map((term, i) =>
      i === index ? { ...term, [key]: value } : term
    );
    setManualTerms(updated);
    setTerms(updated);

    // Unselect template if mismatch
    const selected = termTemplates.find(t => t.name === selectedTemplate);
    if (selected && JSON.stringify(selected.terms) !== JSON.stringify(updated)) {
      setSelectedTemplate(null);
    }
  };

  // const addManualTerm = () => {
  //   setManualTerms([...manualTerms, { label: "", description: "" }]);
  // };

  const addManualTerm = () => {
  const updated = [...manualTerms, { label: "", description: "" }];
  setManualTerms(updated);
  setTerms(updated); // Sync terms
  setSelectedTemplate(null); // Unselect any selected template
};


  const deleteManualTerm = (index) => {
    const updated = manualTerms.filter((_, i) => i !== index);
    setManualTerms(updated);
    setTerms(updated);
    setSelectedTemplate(null);
  };

  if (!open) return null;

  return (
    <div className="dialog-overlay">
      <div className="dialog">
        <div className="dialog-header">
          <h3>Add Terms and Conditions</h3>
          <button onClick={onClose}>X</button>
        </div>
        <div className="tab-buttons">
          <button onClick={() => setActiveTab("template")} className={activeTab === "template" ? "active" : ""}>Template</button>
          <button onClick={() => setActiveTab("manual")} className={activeTab === "manual" ? "active" : ""}>Manual</button>
        </div>

        <div className="tab-content">
          {activeTab === "template" && (
      


            <div className="template-tab">
  <div className="template-list">
    {termTemplates.map((template, index) => {
      const isSelected = selectedTemplate === template.name;
      return (
        <div
          key={index}
          className={`template-item ${isSelected ? "selected" : ""}`}
          onClick={() => setSelectedTemplate(template.name)} // Clicking sets selected template
        >
          <label
            onClick={(e) => e.stopPropagation()} // Prevent click bubbling to div
          >
            <input
              type="checkbox"
              checked={JSON.stringify(terms) === JSON.stringify(template.terms)}
              onChange={(e) => {
                if (e.target.checked) {
                  setTerms(template.terms); // Apply terms
                  setManualTerms(template.terms);
                } else {
                  setTerms([]);
                  setManualTerms([]);
                }
              }}
            />
            {template.name}
          </label>
        </div>
      );
    })}
  </div>

  <div className="template-details">
    {selectedTemplate && (
      <ul>
        {termTemplates
          .find((t) => t.name === selectedTemplate)
          ?.terms.map((term, i) => (
            <li key={i}>
              <strong>{term.label}</strong>: {term.description}
            </li>
          ))}
      </ul>
    )}
  </div>
</div>


          )}

          {activeTab === "manual" && (
            <div className="manual-tab">
              {manualTerms.map((term, index) => (
                <div key={index} className="term-row">
                  <input
                    value={term.label}
                    onChange={(e) => handleManualChange(index, "label", e.target.value)}
                    placeholder="Label"
                  />
                  <input
                    value={term.description}
                    onChange={(e) => handleManualChange(index, "description", e.target.value)}
                    placeholder="Description"
                  />
                  <button onClick={() => deleteManualTerm(index)}>Delete</button>
                </div>
              ))}
              <button onClick={addManualTerm}>Add Term</button>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
