// export default App;
import React from "react";
import { HashRouter as Router, Routes, Route, Navigate } from "react-router-dom";
import Home from "./Home";
import Login from "./Login";
import ProtectedPage from "./ProtectedPage";
import "./App.css";
import UserManagement from "./UserManagement";
import CreatePr from "./CreatePr";
import PreviewPr from "./PreviewPr";
import ApprovePr from "./ApprovePr";
import FinanceApprovePr from "./FinanceApprovePr";
import VendorOnboarding from "./VendorOnboarding";
import PreviewVendor from "./PreviewVendor";
import ApproveVendorOnboarding from "./ApproveVendorOnboarding";
// import CreatePo from "./CreatePo";



const ProtectedRoute = ({ children }) => {
  const isLoggedIn = localStorage.getItem("loggedIn") === "true";
  return isLoggedIn ? children : <Navigate to="/login" />;
};

function App() {
  return (
    <Router>
      <Routes>
        {/* <Route path="/" element={<Home />} /> */}
        <Route path="/" element={<Login />} />
        <Route
          path="/protected"
          element={
            <ProtectedRoute>
              <ProtectedPage />
            </ProtectedRoute>
          }
        />
        <Route
          path="/user-management"
          element={
            <ProtectedRoute>
              <UserManagement />
            </ProtectedRoute>
          }
        />
        <Route
          path="/CreatePr"
          element={
            <ProtectedRoute>
              <CreatePr />
            </ProtectedRoute>
          }
        />
        <Route
          path="/PreviewPr"
          element={
            <ProtectedRoute>
              <PreviewPr />
            </ProtectedRoute>
          }
        />
        <Route
          path="/ApprovePr"
          element={
            <ProtectedRoute>
              <ApprovePr />
            </ProtectedRoute>
          }
        />
        <Route
          path="/FinanceApprovePr"
          element={
            <ProtectedRoute>
              <FinanceApprovePr/>
            </ProtectedRoute>
          }
        />
        <Route
          path="/VendorOnboarding"
          element={
            <ProtectedRoute>
              <VendorOnboarding/>
            </ProtectedRoute>
          }
        />
        <Route
          path="/PreviewVendor"
          element={
            <ProtectedRoute>
              <PreviewVendor/>
            </ProtectedRoute>
          }
        />
        <Route
          path="/ApproveVendorOnboarding"
          element={
            <ProtectedRoute>
              <ApproveVendorOnboarding/>
            </ProtectedRoute>
          }
        />
        {/* <Route
          path="/CreatePo"
          element={
            <ProtectedRoute>
              <CreatePo/>
            </ProtectedRoute>
          }
        /> */}
      </Routes>
    </Router>
  );
}

export default App;
