import React, { useEffect, useState } from "react";
import axios from "axios";
// import "./ApprovePr.css"; 
import "./ApprovedVendorsView.css";

function FinanceApprovePr() {
  const user = JSON.parse(localStorage.getItem("user"));
  const [requests, setRequests] = useState([]);
  const [selectedRequestIndex, setSelectedRequestIndex] = useState(null);
  const [loading, setLoading] = useState(true);
  const [message, setMessage] = useState("");

  useEffect(() => {
    const fetchProcessedPRs = async () => {
      try {
        const response = await axios.get(
          `https://autotask-backend.onrender.com/api/purchase-requests/processed?approverEmail=${user.email}`,
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        );
        setRequests(response.data);
      } catch (err) {
        console.error("Fetch error:", err);
        setMessage("Error fetching processed PRs.");
      } finally {
        setLoading(false);
      }
    };

    if (user?.email) fetchProcessedPRs();
    else {
      setMessage("User not logged in or email missing.");
      setLoading(false);
    }
  }, [user?.email]);

  const handleFinanceAction = async (prId, action) => {
    try {
      await axios.post(
        `https://autotask-backend.onrender.com/api/purchase-requests/${prId}/finance-${action}`,
        {
          approverEmail: user.email,
          department: "Finance",
        },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      setMessage(`✅ PR ${action === "approve" ? "approved" : "rejected"} successfully.`);
      setRequests((prev) => prev.filter((pr) => pr._id !== prId));
      setSelectedRequestIndex(null);
    } catch (err) {
      console.error(err);
      setMessage(`❌ Failed to ${action} PR.`);
    }
  };

  const renderRequestDetails = (pr) => (
    <div className="vendor-details">
      <h3>PR #{pr._id}</h3>
      <p><strong>Requestor:</strong> {pr.requestor}</p>
      <p><strong>Department:</strong> {pr.department}</p>
      <h4>Items:</h4>
      <ul>
        {pr.items.map((item, idx) => (
          <li key={idx}>
            {item.material} — {item.quantity} {item.unit} — {item.description}
          </li>
        ))}
      </ul>

      <div className="button-group">
        <button
          className="approve-btn"
          onClick={() => handleFinanceAction(pr._id, "approve")}
        >
          ✅ Approve Entire PR
        </button>
        <button
          className="reject-btn"
          onClick={() => handleFinanceAction(pr._id, "reject")}
        >
          ❌ Reject Entire PR
        </button>
      </div>
    </div>
  );

  return (
    <div className="approved-vendors-container">
      <h2>Finance Approval - Processed PRs</h2>

      {message && <p className="message">{message}</p>}
      {loading ? (
        <p>Loading...</p>
      ) : (
        <div className="details-list-control">
          <div className="list-control">
            <div className="vendor-list">
              {requests.length === 0 ? (
                <p>No processed PRs to approve.</p>
              ) : (
                requests.map((pr, idx) => (
                  <div
                    key={pr._id}
                    className={`vendor-item ${selectedRequestIndex === idx ? "active" : ""}`}
                    onClick={() => setSelectedRequestIndex(idx)}
                  >
                    PR #{pr._id} — {pr.requestor}
                  </div>
                ))
              )}
            </div>
          </div>

          <div className="vendor-details-section">
            {selectedRequestIndex !== null ? (
              renderRequestDetails(requests[selectedRequestIndex])
            ) : (
              <p>Select a PR to view details and take action.</p>
            )}
          </div>
        </div>
      )}
    </div>
  );
}

export default FinanceApprovePr;
