import React, { useEffect, useState } from "react";
import axios from "axios";
import "./ApprovedVendorsView.css";  // Using same CSS for consistency

function ApprovePr() {
  const user = JSON.parse(localStorage.getItem("user"));
  const [requests, setRequests] = useState([]);
  const [loading, setLoading] = useState(true);
  const [message, setMessage] = useState("");
  const [selectedPRIndex, setSelectedPRIndex] = useState(null);
  const [selectedItems, setSelectedItems] = useState({});

  useEffect(() => {
    const fetchPendingPRs = async () => {
      try {
        const response = await axios.get(
          `https://autotask-backend.onrender.com/api/purchase-requests/pending?approverEmail=${user.email}`,
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        );
        setRequests(response.data);
      } catch (err) {
        console.error("Fetch error:", err);
        setMessage("Error fetching PRs.");
      } finally {
        setLoading(false);
      }
    };

    if (user?.email) {
      fetchPendingPRs();
    } else {
      setMessage("User not logged in or email missing.");
      setLoading(false);
    }
  }, [user?.email]);

  const handleItemSelect = (prId, index) => {
    setSelectedItems((prev) => {
      const key = `${prId}-${index}`;
      return { ...prev, [key]: !prev[key] };
    });
  };

  const handleSelectAll = (prId, totalItems) => {
    const updated = { ...selectedItems };
    for (let i = 0; i < totalItems; i++) {
      updated[`${prId}-${i}`] = true;
    }
    setSelectedItems(updated);
  };

  const handleApproveItems = async (pr) => {
    const approvedItems = pr.items.filter((_, index) => selectedItems[`${pr._id}-${index}`]);
    if (approvedItems.length === 0) {
      setMessage("❌ Please select at least one item to approve.");
      return;
    }

    try {
      await axios.post(
        `https://autotask-backend.onrender.com/api/purchase-requests/${pr._id}/approve-items`,
        {
          approverEmail: user.email,
          department: pr.department,
          approvedItems,
        },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      setMessage("✅ Selected items approved successfully.");
      setRequests((prev) => prev.filter((req) => req._id !== pr._id));
      setSelectedPRIndex(null);
    } catch (err) {
      console.error(err);
      setMessage("❌ Failed to approve items.");
    }
  };

  const handleRejectPR = async (pr) => {
    try {
      await axios.post(
        `https://autotask-backend.onrender.com/api/purchase-requests/${pr._id}/reject`,
        {
          rejectorEmail: user.email,
          department: pr.department,
        },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      setMessage("❌ PR rejected.");
      setRequests((prev) => prev.filter((req) => req._id !== pr._id));
      setSelectedPRIndex(null);
    } catch (err) {
      console.error(err);
      setMessage("❌ Failed to reject PR.");
    }
  };

  const renderPRDetails = (pr) => (
    <div className="vendor-details">
      <h3>PR #{pr._id}</h3>
      <p><strong>Requestor:</strong> {pr.requestor}</p>
      <p><strong>Department:</strong> {pr.department}</p>

      <button onClick={() => handleSelectAll(pr._id, pr.items.length)}>Select All</button>

      <ul>
        {pr.items.map((item, index) => (
          <li key={index}>
            <label>
              <input
                type="checkbox"
                checked={!!selectedItems[`${pr._id}-${index}`]}
                onChange={() => handleItemSelect(pr._id, index)}
              />
              {item.material} — {item.quantity} {item.unit} — {item.description}
            </label>
            {item._id && <div className="item-id">(Item ID: {item._id})</div>}
          </li>
        ))}
      </ul>

      <div className="button-group">
        <button className="approve-btn" onClick={() => handleApproveItems(pr)}>Approve Selected Items</button>
        <button className="reject-btn" onClick={() => handleRejectPR(pr)}>Reject PR</button>
      </div>
    </div>
  );

  return (
    <div className="approved-vendors-container">
      <h2>Pending Purchase Requests</h2>
      {message && <p className="message">{message}</p>}

      {loading ? (
        <p>Loading PRs...</p>
      ) : (
        <div className="details-list-control">
          <div className="list-control">
            <div className="vendor-list">
              {requests.length === 0 ? (
                <p>No pending PRs to approve.</p>
              ) : (
                requests.map((pr, idx) => (
                  <div
                    key={pr._id}
                    className={`vendor-item ${selectedPRIndex === idx ? "active" : ""}`}
                    onClick={() => setSelectedPRIndex(idx)}
                  >
                    PR #{pr._id} | {pr.department}
                  </div>
                ))
              )}
            </div>
          </div>

          <div className="vendor-details-section">
            {selectedPRIndex !== null ? (
              renderPRDetails(requests[selectedPRIndex])
            ) : (
              <p>Select a PR to view and approve/reject items.</p>
            )}
          </div>
        </div>
      )}
    </div>
  );
}

export default ApprovePr;
