const handleExcelSubmit = () => {
  if (!excelFiles || excelFiles.length === 0) {
    alert("Please upload one or more Excel files.");
    return;
  }

  const skippedSheets = [];
  const allParsedVendors = [];

//   const requiredKeyFields = ["vendorname", "vendortype", "gstnumber", "pannumber", "email", "phone"];
  const requiredKeyFields = ["vendorname", "vendortype", "gstnumber", "pannumber"];
  const requiredContactFields = ["name", "email", "phone", "designation"];
  const requiredBankFields = ["bankname", "accountnumber", "ifsccode", "accounttype"];

  const processFile = (file) => {
    return new Promise((resolve) => {
      const reader = new FileReader();
    //   console.log("📥 Reading file:", file.name);

      reader.onload = (e) => {
        try {
          const data = new Uint8Array(e.target.result);
          const workbook = XLSX.read(data, { type: "array" });
        //   console.log("📊 Workbook loaded:", workbook);

          workbook.SheetNames.forEach((sheetName) => {
            // console.log(`📄 Processing File: ${file.name}, Sheet: ${sheetName}`);

            const sheet = workbook.Sheets[sheetName];
            // const raw = XLSX.utils.sheet_to_json(sheet, { header: 1, defval: "", blankrows: false });
            const raw = XLSX.utils.sheet_to_json(sheet, { header: 1, blankrows: false });
            // console.log("raw fields", raw);

            if (!raw || raw.length === 0) {
              skippedSheets.push(`${file.name} - ${sheetName} (Empty Sheet)`);
              return;
            }

            const vendor = {};
            const keyFieldsMap = {};
            let i = 0;

            // Parse Key-Value Pairs
            while (typeof raw[i][1] !== "undefined") {
                // console.log("i entered in while loop");
              const key = raw[i][0].toString().toLowerCase().replace(/\s+/g, "");
              const value = raw[i][1];
              keyFieldsMap[key] = value;
              i++;
            }

            const missingKey = requiredKeyFields.some(
              (key) => !keyFieldsMap[key] || keyFieldsMap[key].toString().trim() === ""
            );
            if (missingKey) {
                // console.log("key_field_map", keyFieldsMap);
              skippedSheets.push(`${file.name} - ${sheetName} (Missing Key Field)`);
              return;
            }

            vendor.vendorName = keyFieldsMap["vendorname"].trim();
            vendor.vendorType = keyFieldsMap["vendortype"].trim();
            vendor.gstNumber = keyFieldsMap["gstnumber"].trim();
            vendor.panNumber = keyFieldsMap["pannumber"].trim();
            // vendor.email = keyFieldsMap["email"].trim();
            // vendor.phone = keyFieldsMap["phone"].trim();

            // Check for Contact Persons section
            if (!raw[i] || raw[i][0]?.toString().toLowerCase().trim() !== "contact person(s)") {
              skippedSheets.push(`${file.name} - ${sheetName} (Missing Contact Section)`);
              return;
            }

            i++;
            const contactHeaders = raw[i].map((h) => h.toString().toLowerCase().replace(/\s+/g, ""));
            //console.log("contactheader", contactHeaders);
            i++;
            const contacts = [];

            while (i < raw.length && raw[i][1]) {
              const row = raw[i];
              if (row[0].toString().toLowerCase().trim() === "bank accounts") break;

              const contact = {};
              contactHeaders.forEach((header, idx) => {
                contact[header] = row[idx] || "";
                
              });

              const missingContact = requiredContactFields.some(
                (field) => !contact[field] || contact[field].toString().trim() === ""
              );
              console.log("contact", contact)
              if (missingContact) {
               //console.log("contact", missingContact);
                skippedSheets.push(`${file.name} - ${sheetName} (Incomplete Contact Row)`);
                return;
              }

              contacts.push({
                name: contact.name.trim(),
                email: contact.email.trim(),
                phone: contact.phone.trim(),
                designation: contact.designation.trim(),
              });

              i++;
            }

            if (contacts.length === 0) {
              skippedSheets.push(`${file.name} - ${sheetName} (No Contact Rows)`);
              return;
            }

            // Check for Bank Accounts section
            if (!raw[i] || raw[i][0]?.toString().toLowerCase().trim() !== "bank account(s)") {
              skippedSheets.push(`${file.name} - ${sheetName} (Missing Bank Section)`);
              return;
            }

            i++;
            const bankHeaders = raw[i].map((h) => h.toString().toLowerCase().replace(/\s+/g, ""));
            i++;
            const bankAccounts = [];

            while (i < raw.length && raw[i][0]) {
              const row = raw[i];
              const bank = {};
              bankHeaders.forEach((header, idx) => {
                bank[header] = row[idx] || "";
              });

              const missingBank = requiredBankFields.some(
                (field) => !bank[field] || bank[field].toString().trim() === ""
              );
              if (missingBank) {
                skippedSheets.push(`${file.name} - ${sheetName} (Incomplete Bank Row)`);
                return;
              }

              bankAccounts.push({
                bankName: bank.bankname.trim(),
                accountNumber: bank.accountnumber.trim(),
                ifscCode: bank.ifsccode.trim(),
                accountType: bank.accounttype.trim(),
              });

              i++;
            }

            if (bankAccounts.length === 0) {
              skippedSheets.push(`${file.name} - ${sheetName} (No Bank Rows)`);
              return;
            }

            vendor.contacts = contacts;
            vendor.bankAccounts = bankAccounts;

            allParsedVendors.push(vendor);
            console.log("✅ Parsed Vendor:", vendor);
          });

          resolve();
        } catch (err) {
          console.error(`❌ Error processing ${file.name}:`, err);
          skippedSheets.push(`${file.name} - ALL SHEETS`);
          resolve();
        }
      };

      reader.readAsArrayBuffer(file);
    });
  };

  // Run all file processing promises
  Promise.all(excelFiles.map((file) => processFile(file))).then(() => {
    console.log("✅ Finished processing all files.");
    console.log("🛑 Skipped sheets:", skippedSheets);
    //console.log("all parsed vendors:", allParsedVendors);

    if (skippedSheets.length > 0) {
      alert(`⚠️ Some sheets were skipped due to validation errors:\n\n${skippedSheets.join("\n")}`);
    }

    if (allParsedVendors.length > 0) {
            //console.log("all parsed vendors:", allParsedVendors);

    //   navigate("/PreviewVendor", { state: { vendors: allParsedVendors } });

    // Step 1: Transform allParsedVendors to formattedItems
        const formattedVendors = allParsedVendors.map((vendor) => ({
        vendorName: vendor.vendorName,
        vendorType: vendor.vendorType,
        gstNumber: vendor.gstNumber,
        panNumber: vendor.panNumber,
        email: vendor.email,
        phone: vendor.phone,
        contacts: vendor.contacts.map((c) => ({
            name: c.name,
            email: c.email,
            phone: c.phone,
            designation: c.designation,
        })),
        bankAccounts: vendor.bankAccounts.map((b) => ({
            bankName: b.bankName,
            accountNumber: b.accountNumber,
            ifscCode: b.ifscCode,
            accountType: b.accountType,
        })),
        }));
        //console.log("formatteditems121:", formattedItems);
        // Step 2: Navigate to preview page with formattedItems
        //navigate("/PreviewVendor", { state: { vendors: formattedVendors } });

         //navigate("/PreviewVendor", { state: { formattedVendors } });

         setVendorData({formattedVendors});
          goToPreviewVendor();


    } else {
      alert("❌ No valid vendors found in uploaded files.");
    }

    setExcelFiles([]); // Clear uploaded files
    setShowDialog(false);
  });
};


