
import React, { useState, useEffect, useMemo } from "react";
import "./PoPreviewView.css";
import axios from "axios";

import AddTermsDialog from "./AddTermsDialog";


const randomCompany = () => ({
  name: "AutoTask Solutions Pvt. Ltd.",
  address: "1234 Tech Park, Bengaluru, India",
  gst: "29ABCDE1234F1Z5",
  pan: "ABCDE1234F",
  contact: "info@autotask.com | +91 98765 43210"
});

// function PoPreviewView({ poDataPreview }) {
function PoPreviewView({ poDataPreview: initialPreview }) {
  const [poDataPreview, setPoDataPreview] = useState(initialPreview);

  //console.log("rfqdatapreview:", poDataPreview);


// Selected vendor index and data still required
const [selectedIndex, setSelectedIndex] = useState(null);
const [selectedVendorData, setSelectedVendorData] = useState(null);

// Store additional charges per vendorId
const [vendorCharges, setVendorCharges] = useState({});

// Store total PO value per vendorId
const [vendorTotals, setVendorTotals] = useState({});

// Dialog visibility per vendorId
const [openChargeDialogs, setOpenChargeDialogs] = useState({});
const [openTermsDialogs, setOpenTermsDialogs] = useState({});

// Track initialization of total value per vendorId
const [hasInitializedPOValue, setHasInitializedPOValue] = useState({});


const [shouldUpdateTotals, setShouldUpdateTotals] = useState({});

const [vendorTerms, setVendorTerms] = useState({});



const selectedVendorId = selectedVendorData?.vendorId;

 const user = JSON.parse(localStorage.getItem("user"));
 const token = localStorage.getItem("token");


  const company = randomCompany();

  const handleSubmit = async () => {
  if (!selectedVendorData || !selectedVendorId) {
    alert("Please select a vendor first.");
    return;
  }

  const groupedItems = {};

  selectedVendorData.lines.forEach((line) => {
    const { itemId, prId, product, hsnSac, quantity, unitOfMeasurement, terms } = line;

    if (!groupedItems[itemId]) {
      groupedItems[itemId] = {
        prId,
        lines: []
      };
    }

    groupedItems[itemId].lines.push({
      product,
      hsnSac,
      quantity,
      unitOfMeasurement,
      terms: terms || []
    });
  });

  const payload = {
    vendorId: selectedVendorId,
    items: groupedItems,
    termsAndConditions: vendorTerms[selectedVendorId] || [],
     createdBy: {
      name: user.email,

    }
  };

  console.log("Submitting payload:", payload);
   try {
    await axios.post(
      "https://autotask-backend.onrender.com/api/rfq-request",
      payload,
      {
        headers: {
          Authorization: `Bearer ${token}`
        }
      }
    );
    alert("✅ RFQ Submitted Successfully");

        // 🔁 Remove the submitted vendor from poDataPreview
setPoDataPreview((prev) => {
  const [itemIdx, vendorIdx] = selectedIndex;

  return prev
    .map((item, i) => {
      if (i !== itemIdx) return item;

      const updatedVendors = item.vendors.filter((_, j) => j !== vendorIdx);
      if (updatedVendors.length === 0) return null;

      return {
        ...item,
        vendors: updatedVendors
      };
    })
    .filter(Boolean); // remove any null entries where all vendors were removed
});

// 🔄 Reset selection & state
setSelectedIndex(null);
setSelectedVendorData(null);

  
  } catch (err) {
    console.error("RFQ Submission Error:", err);
    alert("❌ Failed to submit RFQ");

  }
  
};

  const getVendorName = (vendorId) =>
    `Vendor ${vendorId?.slice(-4).toUpperCase()}`;

  const getVendorLinesFlattened = (vendor) => {
    const allLines = [];
    Object.entries(vendor.items || {}).forEach(([itemId, itemData]) => {
      const { lines, totalValue } = itemData;
      lines.forEach((line) => {
        allLines.push({
          ...line,
          itemId,
          totalValue
        });
      });
    });
    return {
     
      vendorId: vendor.vendorId,
      vendorName: vendor.vendorName || "",
      contacts: vendor.contacts || [],
      gstNumber: vendor.gstNumber || "",
      panNumber: vendor.panNumber || "",
      lines: allLines
    };
  };

 


  
const subTotal = useMemo(() => {
  if (!selectedVendorData) return 0;

  const groupedByItemId = selectedVendorData.lines.reduce((acc, line) => {
    if (!acc[line.itemId]) acc[line.itemId] = [];
    acc[line.itemId].push(line);
    return acc;
  }, {});

  return Object.entries(groupedByItemId).reduce((sum, [, lines]) => {
    const itemTotal = Number(lines?.[0]?.totalValue) || 0;
    return sum + itemTotal;
  }, 0);
}, [selectedVendorData]);

useEffect(() => {
  if (
    !selectedVendorId ||
    hasInitializedPOValue[selectedVendorId] ||
    vendorTotals[selectedVendorId] !== undefined
  )
    return;

  if (subTotal > 0) {
    setVendorTotals((prev) => ({
      ...prev,
      [selectedVendorId]: subTotal
    }));
    setHasInitializedPOValue((prev) => ({
      ...prev,
      [selectedVendorId]: true
    }));
  }
}, [subTotal, selectedVendorId, hasInitializedPOValue, vendorTotals]);







  const renderVendorPO = (vendorData) => {
    const groupedByItemId = vendorData.lines.reduce((acc, line) => {
      if (!acc[line.itemId]) {
        acc[line.itemId] = [];
      }
      acc[line.itemId].push(line);
      return acc;
    }, {});

    let runningSerial = 1;




    return (
      <div key={vendorData.vendorId} className="po-details">
        <h3>Request for Quotation</h3>

        <div className="po-section">
          <strong>From:</strong>
          <p>
            {company.name}
            <br />
            {company.address}
            <br />
            GST: {company.gst}
            <br />
            PAN: {company.pan}
            <br />
            {company.contact}
          </p>
        </div>

         <div className="po-section">
          <strong>To:</strong>
          <p>
            Vendor: {vendorData.vendorName}
            <br />
            Vendor ID: {vendorData.vendorId}
            <br />
            GST: {vendorData.gstNumber}
            <br />
            PAN: {vendorData.panNumber}
            <br />
            {vendorData.contacts?.length > 0 ? (
              <>
                <strong>Contacts:</strong>
                <br />
                {vendorData.contacts.map((contact, index) => (
                  <span key={contact._id || index}>
                    {contact.name} ({contact.designation}) - {contact.phone}, {contact.email}
                    <br />
                  </span>
                ))}
              </>
            ) : (
              <>No contacts available</>
            )}
          </p>
        </div>

        <table className="po-table">
          <thead>
            <tr>
              <th>#</th>
              <th>Description</th>
              <th>HSN/SAC Code</th>
              <th>Qty</th>
              <th>UOM</th>
              <th>Delivery Date</th>
             
            </tr>
          </thead>
          <tbody>
            {Object.entries(groupedByItemId).map(([itemId, lines]) => {
              const totalRows = lines.reduce(
                (count, line) => count + (line.terms?.length > 0 ? 2 : 1),
                0
              );

              return lines.map((line, lineIndex) => {
                const hasTerms = line.terms?.length > 0;
                const showTotalValue = lineIndex === 0;

                return (
                  <React.Fragment key={`${itemId}-${lineIndex}`}>
                    <tr>
                      <td rowSpan={hasTerms ? 2 : 1}>{runningSerial++}</td>
                      <td>{line.product || "N/A"}</td>
                      <td>{line.hsnSac || "N/A"}</td>
                      <td>{line.quantity}</td>
                      <td>{line.unitOfMeasurement}</td>
                      <td>{new Date(line.deliveryDate).toLocaleDateString("en-GB")}</td>

                     
                    </tr>
                    {hasTerms && (
                      <tr className="terms-row">
                        <td colSpan={7}>
                          <strong>Terms:</strong>
                          <ul>
                            {line.terms.map((t, i) => (
                              <li key={i}>
                                {t.label}: {t.description}
                              </li>
                            ))}
                          </ul>
                        </td>
                      </tr>
                    )}
                  </React.Fragment>
                );
              });
            })}
          </tbody>
          <tfoot>
     
      {vendorTerms[selectedVendorId] && vendorTerms[selectedVendorId].length > 0 && (
  <tr>
    <td
      colSpan={9}
      style={{ textAlign: "left", fontWeight: "bold", whiteSpace: "pre-line" }}
    >
      {"Terms:\n" +
        vendorTerms[selectedVendorId]
          .map((term, index) => `${index + 1}. ${term.label}: ${term.description}`)
          .join("\n")}
    </td>
  </tr>
)}

          </tfoot>
        </table>
    

<AddTermsDialog
  open={openTermsDialogs[selectedVendorId] || false}
  onClose={() =>
    setOpenTermsDialogs((prev) => ({
      ...prev,
      [selectedVendorId]: false
    }))
  }
  terms={vendorTerms[selectedVendorId] || []}
  setTerms={(newTerms) =>
    setVendorTerms((prev) => ({
      ...prev,
      [selectedVendorId]: newTerms
    }))
  }
/>




      </div>
    );
  };

  return (
    <div className="po-preview-container">
      <h2>RFQ View</h2>
      <div className="details-list-control">
        <div className="list-control">
          <div className="vendor-list">
            {poDataPreview.flatMap((item, i) =>
              item.vendors.map((vendor, j) => (
                <div
                  key={`${i}-${j}`}
                  className={`vendor-item ${
                    selectedIndex?.[0] === i && selectedIndex?.[1] === j ? "active" : ""
                  }`}


                  onClick={() => {
                    setSelectedIndex([i, j]);
                    const vendorData = getVendorLinesFlattened(poDataPreview[i].vendors[j]);
                    setSelectedVendorData(vendorData);
                    setHasInitializedPOValue((prev) => ({
                      ...prev,
                      [poDataPreview[i].vendors[j].vendorId]: false
                    }));
                  }}

                >
               #{vendor.vendorName} | ID: {vendor.vendorId}
                </div>
              ))
            )}
          </div>
        </div>

        <div className="vendor-details-section">






  {selectedVendorData && (
  <div className="vendor-action-buttons">
 
    <button
      onClick={() =>
        setOpenTermsDialogs((prev) => ({
          ...prev,
          [selectedVendorId]: true
        }))
      }
    >
      Add Terms and Conditions
    </button>
  </div>
)}





          {selectedVendorData ? renderVendorPO(selectedVendorData) : <p>Select a vendor to preview the PO.</p>}
           {selectedVendorData && (
          <button
          onClick={handleSubmit}>
        
          Submit
        </button>
           )}
        </div>
      </div>
    </div>
  );
}

export default PoPreviewView;
