
import React, { useState, useRef, useEffect } from 'react';
import { useNavigate } from "react-router-dom";
import "./ProtectedPage.css";

// Import your page components
import UserManagement from "./UserManagement";
import CreatePr from "./CreatePr";
import ApprovePr from "./ApprovePr";
import FinanceApprovePr from "./FinanceApprovePr";
import VendorOnboarding from "./VendorOnboarding";
import ApproveVendorOnboarding from "./ApproveVendorOnboarding";
import PreviewPr from "./PreviewPr";
import PreviewVendor from "./PreviewVendor";
import ApprovedVendorsView from "./ApprovedVendorsView";
import PendingPo from "./PendingPo";
import CreateSelectedVendorPo from "./CreateSelectedVendorPo";
import CreateSelectedPo from "./CreateSelectedPo";
import ApprovePoPerVendor from "./ApprovePoPerVendor";
import PoPreviewView from "./PoPreviewView";
import CreateRfq from "./CreateRfq";
import CreateSelectedRfq from "./CreateSelectedRfq";
import RfqPreviewView from "./RfqPreviewView";


// Add more components as necessary

function ProtectedPage() {
  const navigate = useNavigate();
  const user = JSON.parse(localStorage.getItem("user"));
  const [showDropdown, setShowDropdown] = useState(false);
  const [sidebarCollapsed, setSidebarCollapsed] = useState(false);
  const [activeComponent, setActiveComponent] = useState(null);
  const [prData, setPrData] = useState(null);
  const [vendorData, setVendorData] = useState(null);
  const [poData, setPoData] = useState(null);
  const [rfqData, setRfqData] = useState(null);
  const containerRef = useRef(null);
  const dialogRef = useRef(null);
  const sidebarRef = useRef(null);
  const [poDataPreview, setPoDataPreview] = useState(null);
  const [rfqDataPreview, setRfqDataPreview] = useState(null);


  const handleLogout = () => {
    localStorage.removeItem("token");
    localStorage.removeItem("user");
    localStorage.removeItem("loggedIn");
    navigate("/");
  };

//  useEffect(() => {
//     return () => {
//       // This cleanup function runs on unmount (i.e., navigating away)
//       handleLogout();
//     };
//   }, []);

useEffect(() => {
  const sidebar = sidebarRef.current;
  if (!sidebar) return;

  const handleTransitionEnd = (e) => {
    if (e.propertyName === 'width') {
      console.log('Sidebar width transition ended');
      updateDialogPosition();
    }
  };

  sidebar.addEventListener('transitionend', handleTransitionEnd);

  return () => {
    sidebar.removeEventListener('transitionend', handleTransitionEnd);
  };
}, []);

    const updateDialogPosition = () => {
      // console.log("updatedialogposition entered");
  //     console.log('containerRef.current:', containerRef.current);
  // console.log('dialogRef.current:', dialogRef.current);
    if (containerRef.current && dialogRef.current) {
      //  console.log("updatedialogposition entered");
      const containerRect = containerRef.current.getBoundingClientRect();
      const dialog = dialogRef.current;

      // Center dialog within the container
      const containerCenterX = containerRect.left + containerRect.width / 2;
      dialog.style.left = `${containerCenterX}px`;
    }
  };

  const handlePrivilegeClick = (privilege) => {
    setActiveComponent(privilege);
  };

  const goToPreviewPr = () => {
    setActiveComponent("preview_pr");
  };

  const goBackToCreatePr = () => {
    setActiveComponent("create_pr");
  };

  const goToPreviewVendor = () => {
    setActiveComponent("preview_vendor");
  };

  const goBackToCreateVendor = () => {
    setActiveComponent("vendor_onboarding");
  };

  const goToCreateSelectedPo = () => {
    setActiveComponent("create_selected_po");
  };

  const goToCreateSelectedVendorPo = () => {
    setActiveComponent("create_selected_vendor_po");
  };

  const goToCreateSelectedRfq = () => {
    setActiveComponent("create_selected_rfq");
  };

  const goBackToPendingPo = () => {
    setActiveComponent("create_po");
  };

  const goToPoPreviewView = ()=> {
    setActiveComponent("po_preview_view");
  }

  const goToRfqPreviewView = ()=> {
    setActiveComponent("rfq_preview_view");
  }

  const renderActiveComponent = () => {
    switch (activeComponent) {
      case "manage_users":
        return <UserManagement />;
      case "create_pr":
         return <CreatePr goToPreviewPr={goToPreviewPr} setPrData={setPrData} prData={prData} />;
      case "preview_pr":
        return <PreviewPr goBack={goBackToCreatePr} setPrData={setPrData} prData={prData} />;
      case "approve_pr":
        return <ApprovePr />;
      case "finance_approve_pr":
        return <FinanceApprovePr />;
      case "vendor_onboarding":
        return <VendorOnboarding goToPreviewVendor={goToPreviewVendor} setVendorData={setVendorData} vendorData={vendorData} />;
      case "preview_vendor":
        return <PreviewVendor goBack={goBackToCreateVendor} setVendorData={setVendorData} vendorData={vendorData}/>;
      case "approve_vendor_onboarding":
        return <ApproveVendorOnboarding />;
      case "approved_vendors_view":
        return <ApprovedVendorsView />;
      case "create_po":
        return <PendingPo goToCreateSelectedPo={goToCreateSelectedPo} goToCreateSelectedVendorPo={goToCreateSelectedVendorPo} setPoData={setPoData} />;
      case "create_selected_po":
        return <CreateSelectedPo goBack={goBackToPendingPo} poData={poData} setPoData={setPoData} containerRef= {containerRef} dialogRef={dialogRef} updateDialogPosition={updateDialogPosition} poDataPreview={poDataPreview} setPoDataPreview={setPoDataPreview} goToPoPreviewView={goToPoPreviewView}/>;
      case "approve_po":
        return <ApprovePoPerVendor dialogRef={dialogRef} updateDialogPosition={updateDialogPosition}/>;
      case "po_preview_view":
        return <PoPreviewView poDataPreview={poDataPreview} setPoDataPreview={setPoDataPreview}/>;
      case "create_rfq":
        return <CreateRfq goToCreateSelectedRfq={goToCreateSelectedRfq} setRfqData={setRfqData} />;
      case "create_selected_rfq":
        return <CreateSelectedRfq poData={rfqData} containerRef= {containerRef} dialogRef={dialogRef} updateDialogPosition={updateDialogPosition} rfqDataPreview={rfqDataPreview} setRfqDataPreview={setRfqDataPreview} goToRfqPreviewView={goToRfqPreviewView}/>;
      case "rfq_preview_view":
        return <RfqPreviewView poDataPreview={rfqDataPreview} />;
       case "create_selected_vendor_po":
        return <CreateSelectedVendorPo goBack={goBackToPendingPo} poData={poData} setPoData={setPoData} containerRef= {containerRef} dialogRef={dialogRef} updateDialogPosition={updateDialogPosition} poDataPreview={poDataPreview} setPoDataPreview={setPoDataPreview} goToPoPreviewView={goToPoPreviewView}/>;
      default:
        return <p>Please select an option from the sidebar.</p>;
    }
  };

  return (
    <div className="dashboard-container">
      {/* Top Bar */}
      <div className="top-bar">
        <div className="company-name">MUSKANT</div>
        <div className="user-info" onClick={() => setShowDropdown(!showDropdown)}>
          {user.name} <span className="arrow">▼</span>
          {showDropdown && (
            <div className="dropdown">
              <p><strong>Role:</strong> {user.role}</p>
              <p><strong>Dept:</strong> {user.department}</p>
              <button onClick={handleLogout}>Logout</button>
            </div>
          )}
        </div>
      </div>

      <div className="main-content">
        {/* Sidebar */}
        <div className={`sidebar ${sidebarCollapsed ? "collapsed" : ""}`} ref={sidebarRef}>
          <button
            className="collapse-btn"
            onClick={() => setSidebarCollapsed(!sidebarCollapsed)}
          >
            {sidebarCollapsed ? "➤" : "⮜"}
          </button>
          {!sidebarCollapsed &&
            user.privileges.map((priv, index) => (
              <button
                key={index}
                className={`sidebar-btn ${activeComponent === priv ? "active" : ""}`}
                onClick={() => handlePrivilegeClick(priv)}
              >
                {priv.replace(/_/g, " ").toUpperCase()}
              </button>
            ))}
        </div>

        {/* Main Panel */}
        <div className="content-area" ref={containerRef}>
          {renderActiveComponent()}
        </div>
      </div>
    </div>
  );
}

export default ProtectedPage;
