// src/PreviewPr.js
import React, { useState } from "react";
import axios from "axios";
import "./PreviewPr.css";

function PreviewPr({ prData, setPrData, goBack }) {
  const token = localStorage.getItem("token");

  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitSuccess, setSubmitSuccess] = useState(false);

  if (!prData) {
    return <div>No PR data to preview.</div>;
  }

  const { requestor, department, items } = prData;

  const handleSubmit = async () => {
    setIsSubmitting(true);
    try {
      await axios.post(
        "https://autotask-backend.onrender.com/create-pr",
        { requestor, department, items },
        { headers: { Authorization: `Bearer ${token}` } }
      );
      alert("✅ PR Submitted Successfully");
      setIsSubmitting(false);
      setSubmitSuccess(true);
      setPrData(null);
      goBack();

    } catch (err) {
      alert("❌ Failed to submit PR");
      setIsSubmitting(false);
    }
  };

  return (
    <div className="preview-wrapper">
      <h2>📝 Preview Purchase Request</h2>
      <p><strong>Requestor:</strong> {requestor}</p>
      <p><strong>Department:</strong> {department}</p>

      {items.map((item, idx) => (
        <div key={idx} className="preview-item">
          <h4>📦 Item {idx + 1}</h4>
          <p><strong>Material:</strong> {item.material}</p>
          <p><strong>Quantity:</strong> {item.quantity}</p>
          <p><strong>Unit:</strong> {item.unit}</p>
          <p><strong>Delivery Date:</strong> {new Date(item.deliveryDate).toLocaleDateString()}</p>
          <p><strong>Cost Center:</strong> {item.costCenter}</p>
          <p><strong>Description:</strong> {item.description}</p>
        </div>
      ))}

      <div className="preview-actions">
        {isSubmitting ? (
          <div className="loader"></div>
        ) : submitSuccess ? (
          <p className="success-message">✅ PR Submitted Successfully!</p>
        ) : (
          <>
            <button onClick={goBack} disabled={isSubmitting}>✏️ Edit</button>
            <button onClick={handleSubmit} disabled={isSubmitting}>✅ Submit</button>
          </>
        )}
      </div>
    </div>
  );
}

export default PreviewPr;
