import React, { useState } from "react";
import { useLocation, useNavigate } from 'react-router-dom';
import './PreviewVendor.css';
import axios from "axios";

const PreviewVendor = ({ goBack, setVendorData, vendorData }) => {
  const navigate = useNavigate();
  const token = localStorage.getItem("token");
  const location = useLocation();
    const user = JSON.parse(localStorage.getItem("user"));

    const [isSubmitting, setIsSubmitting] = useState(false);
      const [submitSuccess, setSubmitSuccess] = useState(false);

  const { formattedVendors } = vendorData || {};
  //const {allParsedVendors} = location.state || {};

//   const handleFinalSubmit = async () => {
//     try {
//       const response = await fetch('/api/vendor-onboarding', {
//         method: 'POST',
//         headers: {
//           'Content-Type': 'application/json',
//           Authorization: `Bearer ${token}`, // Include token if needed
//         },
//         body: JSON.stringify({ vendors: formattedVendors }),
//       });

//       if (!response.ok) throw new Error('Submission failed');
//       alert('✅ Vendor(s) submitted successfully!');
//       navigate('/protected');
//     } catch (error) {
//       console.error(error);
//       alert('❌ Submission error');
//     }
//   };

  const handleFinalSubmit = async () => {
    setIsSubmitting(true);
    try {
    const requestor = user.name; 
    const department = user.department;
      await axios.post(
        "https://autotask-backend.onrender.com/create-vendor",
        
        { requestor, department, formattedVendors },
        { headers: { Authorization: `Bearer ${token}` } }
      );
      alert("✅ PR Submitted Successfully");
      setIsSubmitting(false);
      setSubmitSuccess(true);
      // navigate("/protected");
      setVendorData(null);
      goBack();
    } catch (err) {
      alert("❌ Failed to submit PR");
      goBack();
    }
  };

  // const handleEdit = () => {
  //   navigate('/VendorOnboarding', { state: { vendors: formattedVendors } });
  // };

  if (!formattedVendors || formattedVendors.length === 0) {
    return <div>No vendor data to preview.</div>;
  }

  return (
    <div className="preview-container">
      <h2>Vendor Onboarding Preview</h2>

      {formattedVendors.map((vendor, index) => (
        <div key={index} className="vendor-preview">
          {/* <h3>{vendor.vendorNumber}</h3> */}
        <h3 className="vendor-heading">Vendor - {index + 1}</h3>


          <div className="section">
            <h4>Basic Information</h4>
            <p><strong>Name:</strong> {vendor.vendorName}</p>
            <p><strong>Type:</strong> {vendor.vendorType}</p>
            <p><strong>GST Number:</strong> {vendor.gstNumber}</p>
            <p><strong>PAN Number:</strong> {vendor.panNumber}</p>
            {/* <p><strong>Email:</strong> {vendor.email}</p>
            <p><strong>Phone:</strong> {vendor.phone}</p> */}
          </div>

          <div className="section">
            <h4>Contact Persons</h4>
            {vendor.contacts.map((c, i) => (
              <div key={i} className="preview-box">
                <p><strong>Contact {i + 1}</strong></p>
                <p>Name: {c.name}</p>
                <p>Email: {c.email}</p>
                <p>Phone: {c.phone}</p>
                <p>Designation: {c.designation}</p>
              </div>
            ))}
          </div>

          <div className="section">
            <h4>Bank Accounts</h4>
            {vendor.bankAccounts.map((b, i) => (
              <div key={i} className="preview-box">
                <p><strong>Account {i + 1}</strong></p>
                <p>Bank: {b.bankName}</p>
                <p>Account Number: {b.accountNumber}</p>
                <p>IFSC Code: {b.ifscCode}</p>
                <p>Account Type: {b.accountType}</p>
              </div>
            ))}
          </div>

          <hr />
        </div>
      ))}

      {/* <div className="button-group">
        <button className="edit-button" onClick={goBack}>✏️ Edit</button>
        <button className="submit-button" onClick={handleFinalSubmit}>✅ Submit</button>
      </div> */}
      <div className="preview-actions">
        {isSubmitting ? (
          <div className="loader"></div>
        ) : submitSuccess ? (
          <p className="success-message">✅ PR Submitted Successfully!</p>
        ) : (
          <>
            <button onClick={goBack} disabled={isSubmitting}>✏️ Edit</button>
            <button onClick={handleFinalSubmit} disabled={isSubmitting}>✅ Submit</button>
          </>
        )}
      </div>
    </div>
  );
};

export default PreviewVendor;
