

import React, { useState, useEffect } from "react";
// import { useNavigate, useLocation } from "react-router-dom";
import "./CreatePr.css";
import * as XLSX from "xlsx";

// function CreatePr() {
function CreatePr({ goToPreviewPr, setPrData, prData}) {

  const user = JSON.parse(localStorage.getItem("user"));
//   const navigate = useNavigate();
//   const location = useLocation();

  const [items, setItems] = useState([
    { material: "", quantity: "", unit: "", deliveryDate: "", costCenter: "", description: "" },
  ]);
  const [message, setMessage] = useState("");

  const [showDialog, setShowDialog] = useState(false); // ✅ Modal toggle
  const [excelFile, setExcelFile] = useState(null); // ✅ Uploaded file state



useEffect(() => {
    if (prData) {

        const updatedItems = prData.items.map((item) => ({
       ...item,
         deliveryDate: item.deliveryDate
           ? new Date(item.deliveryDate).toISOString().split("T")[0]
           : "",
       }));
      setItems(updatedItems);
    }
  }, [prData]);

  const handleChange = (index, e) => {
    const { name, value } = e.target;
    const updatedItems = [...items];
    updatedItems[index][name] = value;
    setItems(updatedItems);
  };

  const handleAddItem = () => {
    setItems([
      ...items,
      { material: "", quantity: "", unit: "", deliveryDate: "", costCenter: "", description: "" },
    ]);
  };

  const handleRemoveItem = (indexToRemove) => {
    if (items.length === 1) return;
    const updatedItems = items.filter((_, index) => index !== indexToRemove);
    setItems(updatedItems);
  };

  const validateItems = () => {
    for (let i = 0; i < items.length; i++) {
      const item = items[i];
      if (
        !item.material.trim() ||
        !item.quantity ||
        isNaN(item.quantity) ||
        Number(item.quantity) <= 0 ||
        !item.unit.trim() ||
        !item.deliveryDate ||
        !item.costCenter.trim() ||
        !item.description.trim()
      ) {
        return `Please fill all fields correctly for Item ${i + 1}`;
      }
    }
    return null;
  };

  const handlePreview = (e) => {
    e.preventDefault();
    setMessage("");

    const validationError = validateItems();
    if (validationError) {
      setMessage(`❌ ${validationError}`);
      return;
    }

    const formattedItems = items.map((item) => ({
      material: item.material.trim(),
      quantity: Number(item.quantity),
      unit: item.unit.trim(),
      deliveryDate: new Date(item.deliveryDate).toISOString(),
      costCenter: item.costCenter.trim(),
      description: item.description.trim(),
    }));

    setPrData({
  requestor: user.name,
  department: user.department,
  items: formattedItems,
});
  goToPreviewPr();
  };

  const handleExcelChange = (e) => {
    setExcelFile(e.target.files[0]);
  };



const handleExcelSubmit = () => {
  if (!excelFile) {
    alert("Please upload an Excel file.");
    return;
  }

  const reader = new FileReader();

  reader.onload = (e) => {
    const data = new Uint8Array(e.target.result);
    const workbook = XLSX.read(data, { type: "array" });
    const sheetName = workbook.SheetNames[0];
    const worksheet = workbook.Sheets[sheetName];

    // Normalize header keys
    const rawJson = XLSX.utils.sheet_to_json(worksheet, {
      header: 1, // Get raw rows
      defval: "", // Prevent undefined values
    });

    const headers = rawJson[0].map((h) =>
      h.toString().toLowerCase().replace(/\s+/g, "")
    );

    const expectedKeys = [
      "material",
      "quantity",
      "unit",
      "deliverydate",
      "costcenter",
      "description",
    ];

    const missing = expectedKeys.filter((key) => !headers.includes(key));
    if (missing.length > 0) {
      alert(`❌ Missing columns: ${missing.join(", ")}`);
      return;
    }

    const formattedItems = rawJson.slice(1).map((row, index) => {
      const obj = {};
      headers.forEach((key, i) => {
        obj[key] = row[i];
      });

      return {
        material: String(obj.material || "").trim(),
        quantity: Number(obj.quantity),
        unit: String(obj.unit || "").trim(),
        deliveryDate: new Date(obj.deliverydate).toISOString(),
        costCenter: String(obj.costcenter || "").trim(),
        description: String(obj.description || "").trim(),
      };
    });

    setPrData({
  requestor: user.name,
  department: user.department,
  items: formattedItems,
});

    

  

    setShowDialog(false);
    setExcelFile(null);
    goToPreviewPr();
  };

  reader.readAsArrayBuffer(excelFile);
};

  const handleDownloadSample = () => {
  window.electronAPI.downloadSampleExcel();
};

    return (
  <>
    <div className={`create-pr-wrapper ${showDialog ? "blurred" : ""}`}>
      <div className="top-right-home">
        <button onClick={() => setShowDialog(true)}>📤 Upload Excel</button>
      </div>

      <h2>Create Purchase Request</h2>

      <form onSubmit={handlePreview} className="pr-form">
        {items.map((item, index) => (
          <div key={index} className="pr-item-card">
            <div className="pr-card-header">
              <h4>Item {index + 1}</h4>
              {items.length > 1 && (
                <button
                  type="button"
                  className="delete-btn"
                  onClick={() => handleRemoveItem(index)}
                >
                  🗑️ Remove
                </button>
              )}
            </div>

            <div className="pr-grid">
              <div className="form-group">
                <label>Material</label>
                <input
                  name="material"
                  value={item.material}
                  onChange={(e) => handleChange(index, e)}
                  required
                />
              </div>

              <div className="form-group">
                <label>Quantity</label>
                <input
                  name="quantity"
                  type="number"
                  min="1"
                  value={item.quantity}
                  onChange={(e) => handleChange(index, e)}
                  required
                />
              </div>

              <div className="form-group">
                <label>Unit</label>
                <input
                  name="unit"
                  value={item.unit}
                  onChange={(e) => handleChange(index, e)}
                  required
                />
              </div>

              <div className="form-group">
                <label>Delivery Date</label>
                <input
                  name="deliveryDate"
                  type="date"
                  value={item.deliveryDate}
                  onChange={(e) => handleChange(index, e)}
                  required
                />
              </div>

              <div className="form-group">
                <label>Cost Center</label>
                <input
                  name="costCenter"
                  value={item.costCenter}
                  onChange={(e) => handleChange(index, e)}
                  required
                />
              </div>

              <div className="form-group full-width">
                <label>Description</label>
                <textarea
                  name="description"
                  value={item.description}
                  onChange={(e) => handleChange(index, e)}
                  required
                />
              </div>
            </div>
          </div>
        ))}

        <div className="form-actions">
          <button type="button" onClick={handleAddItem}>➕ Add Item</button>
          <button type="submit">📋 Preview PR</button>
        </div>
      </form>

      {message && <p className="status-message">{message}</p>}
    </div>

    {showDialog && (
      <div className="dialog-overlay">
        <div className="dialog-box">
          <h3>Upload Excel file to fetch PR details</h3>
          <p>
            Please upload a valid Excel file with columns as shown in the{" "}
            <button type="button" onClick={handleDownloadSample}>
              sample sheet
            </button>.
          </p>
          <p>The Excel file must have the requisite columns stated in the sample.</p>
          <input type="file" accept=".xlsx, .xls" onChange={handleExcelChange} />
          <div className="dialog-actions">
            <button onClick={handleExcelSubmit}>📤 Upload</button>
            <button onClick={() => setShowDialog(false)}>❌ Close</button>
          </div>
        </div>
      </div>
    )}
  </>
);

}

export default CreatePr;



